:root{--primary-color:#6E6E6E;--secondary-color: #C4C4C4;--light-color: #FFFF;--dark-color: #4E4E4E;--shadow-header: 7px 7px 0px #8C0200;--fs-header: 85pt;--fs-header-alt: 48pt;--fs-title: 28pt;--fs-menu-bar: 28pt;--fs-caption: 18pt;--fs-button: 40pt}*{margin:0;padding:0;box-sizing:border-box}body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;overflow-x:hidden}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}::-webkit-scrollbar{width:5px}::-webkit-scrollbar-track{background:#ffffff}::-webkit-scrollbar-thumb{background:#8C0200;border-radius:10px}::-webkit-scrollbar-thumb:hover{background:#770301}

.App{text-align:center;width:100vw;height:100vh}.custom{font-size:200px}

.Navbar{position:fixed;width:100vw;height:80px;display:flex;justify-content:space-around;transition:height .3s ease-in-out;z-index:99;background-color:rgba(0,0,0,0.2)}.Navbar__logo{margin-top:1.4%;margin-left:5%;height:120%;align-self:center;transition:.1s ease-out}.Navbar__menu{display:flex;flex-flow:row nowrap;justify-content:center;align-items:center;list-style:none;font-family:'Playfair Display', serif;height:100%;width:100%;color:var(--light-color)}.Navbar__item{font-size:28px;margin:0 1.5em;text-decoration:none;transition:.2s ease-out;color:#ffff;margin:0 50px}.Navbar__item:hover{color:red;text-decoration:none}.Navbar .hamburger{display:none;z-index:1;top:35%;cursor:pointer}@media screen and (max-width: 1173px){.Navbar .hamburger{display:block}.Navbar .Navbar__menu{flex-flow:column nowrap;background:#282828;position:fixed;line-height:60px;top:0;right:0;height:100vh;width:300px;transform:translateX(0);transition:transform .3s ease-out}.Navbar .hide{transform:translateX(100%)}}@media (max-width: 420px){.Navbar .Navbar__menu{width:200px}.Navbar .Navbar__menu .Navbar__item{font-size:1rem}}.is-current{color:#DCA04D}.scrolled{height:65px;background-color:rgba(0,0,0,0.8)}.scrolled .Navbar__logo{margin-top:1.1%}.scrolled img{transform:scale(0.95)}

