nsform:rotateZ(-15deg) translateY(70px);right:100%}100%{transform:rotateZ(0deg);right:120%}}@-webkit-keyframes animateWhale2{0%{transform:rotateZ(0deg);left:-30%}25%{transform:rotateZ(10deg) translateY(50px);left:20%}50%{transform:rotateZ(0deg);left:60%}75%{transform:rotateZ(15deg) translateY(40px);left:60%}100%{transform:rotateZ(0deg);left:120%}}@keyframes animateWhale2{0%{transform:rotateZ(0deg);left:-30%}25%{transform:rotateZ(10deg) translateY(50px);left:20%}50%{transform:rotateZ(0deg);left:60%}75%{transform:rotateZ(15deg) translateY(40px);left:60%}100%{transform:rotateZ(0deg);left:120%}}

#Wish{display:flex;justify-content:center;align-items:center;background:url(/react-hello-christmas/static/media/iceberg.6181cb4f.png) no-repeat left bottom;background-color:#9AC0CB;background-size:100%;min-height:100%}#Wish .Wish__header h1{font-size:50px;font-family:'Cardo', serif;font-weight:700;line-height:180%;color:#000000;text-shadow:2px 2px 5px rgba(0,0,0,0.7)}#Wish .Wish__header p{font-family:'Cardo', serif;font-size:28px;font-weight:400;text-shadow:2px 2px 6px #FFFFFF}#Wish .Wish__container{margin:7vh 0;max-width:80%}#Wish .Wish__container img{width:400px;-webkit-animation:animate-pulse 3s infinite;animation:animate-pulse 3s infinite;margin:1em 0}#Wish .Wish__button{display:block;margin:0 auto;background:url(/react-hello-christmas/static/media/wish_button.dd981cc9.png) no-repeat;background-size:100%;cursor:pointer;width:525px;height:160px;transition:.2s ease-out}#Wish .Wish__button:hover{-webkit-filter:drop-shadow(0px 4px 4px #DCA04D);filter:drop-shadow(0px 4px 4px #DCA04D);transform:scale(1.02)}#Wish .Wish__button:active{transition:none;background:url(/react-hello-christmas/static/media/wish_button_clicked.cf580abe.png) no-repeat;background-size:100%;width:525px;height:166px}#Wish .wishes-whale{position:absolute;z-index:2}#Wish .wishes-whale .landscape{-webkit-animation:whale-idle 2s infinite;animation:whale-idle 2s infinite;width:90%}#Wish .wishes-whale .portrait{-webkit-animation:whale-idle 2s infinite;animation:whale-idle 2s infinite;width:80%}#Wish .fadeBlack{position:fixed;background:rgba(0,0,0,0.5);left:0;top:0;width:100%;height:100%;z-index:2}@media screen and (max-width: 1440px){#Wish .Wish__header h1{font-size:44px}#Wish .Wish__header p{font-size:24px}#Wish .Wish__container img{width:300px}#Wish .Wish__button{width:350px}#Wish .Wish__button:active{width:350px}}@media screen and (max-width: 1024px){#Wish .Wish__header h1{font-size:38px}#Wish .Wish__header p{font-size:22px}}@media screen and (max-width: 1024px){#Wish .Wish__header h1{font-size:28px}