#Footer{color:#ffffff;position:relative;background:url(/react-hello-christmas/static/media/props-bg.13d92ba3.png) no-repeat;background-position:bottom right;background-size:100%;background-color:#6E0E29;font-family:'Playfair Display', serif}#Footer .big-bell{position:absolute;bottom:0;left:-1.3in;width:20%;z-index:1}#Footer .bell{position:absolute;width:50px;top:-10%;left:20%;transform:rotateZ(30deg);z-index:1}#Footer .bell2{position:absolute;width:50px;top:-10%;left:55%;transform:rotateZ(-30deg);z-index:1}#Footer .tree{position:absolute;width:50px;top:-10%;left:75%;transform:rotateZ(30deg);z-index:0}@media screen and (max-width: 1024px){#Footer .big-bell{top:-1in}}@media screen and (max-width: 768px){#Footer .big-bell{left:-1in}}@media screen and (max-width: 500px){#Footer .big-bell{display:none}}.Footer__container{margin:0 auto;max-width:80%;padding:1.2rem}.Footer__container ul{list-style:none;display:flex;justify-content:space-evenly;align-items:center;flex-wrap:wrap;min-height:339px}.Footer__container ul img{display:block;width:100px;border-radius:50px;transition:.3s ease-out;z-index:5}.Footer__container ul img:hover{transform:scale(1.1) translateY(-10px);box-shadow:0px 0px 20px #DCA04D}.Footer__container ul li{line-height:12px}.Footer__container ul .info{margin:1.5rem 0}.Footer__container ul .name{font-size:20px}.Footer__container ul .work{font-size:18px}

